.menu-panel {
  border-radius: 15px;
  padding: 1.5vw;
  box-shadow: 0px 5px 5px var(--shadecolor);
  width: 80%;
  margin: 10px;
  height: auto;
  background-color: var(--maincolor);
}

.inset-panel {
  border-radius: 15px;
  padding: 1.5vw;
  box-shadow: 0px 5px 5px var(--shadecolor) inset;
  width: 80%;
  margin: 10px;
  height: auto;
  background-color: var(--maintint);
  backdrop-filter: blur(5px);
}

/* Maps layout */
.maps-columns {
  display: flex;
  height: 100%;
}

.maps-columns .left-column,
.maps-columns .right-column {
  flex: 0 0 20%;
  flex-direction: column;
  align-items: center;
  display: flex;
  background-color: var(--main2color);
}

.maps-columns .center-column {
  flex: 1;
  background-color: var(--maincolor);
  padding: 10px;
  box-shadow: 0px 0px 5px var(--shadecolor);
}


/* Polls/Graphs Layout */
.two-column-layout {
  display: flex;
  height: 100%;
}

.two-column-layout .left-column {
  flex: 0 0 40%;
  background-color: var(--main2color);
}

.two-column-layout .right-column {
  flex: 1;
  background-color: var(--maincolor);
  padding: 20px;
  box-shadow: 0px 0px 5px var(--shadecolor);
}

/* About Layout */
.about-columns {
  display: flex;
  height: 100%;
}

.about-columns .left-column {
  flex: 0 0 30%;
  background-color: var(--main2color);
}

.about-columns .center-column {
  flex: 1;
  background-color: var(--maincolor);
  padding: 20px;
  box-shadow: 0px 0px 5px var(--shadecolor);
}

.about-columns .right-column {
  flex: 0 0 30%;
  background-color: var(--main2color);
}

/* Responsive Adjustments at 960px */
@media (max-width: 960px) {

  /* Maps Page Responsive Layout*/
  .maps-columns {
    flex-direction: column;
  }

  .maps-columns .center-column {
    order: 1;
    padding: 20px;
  }

  .maps-columns .left-column {
    order: 2;
  }

  .maps-columns .right-column {
    order: 3;
  }

  /* Polls and Graphs Responsive Layout */
  .two-column-layout {
    flex-direction: column;
  }

  .two-column-layout .left-column {
    height: calc(50% - 20px);
    overflow-y: auto;
  }

  .two-column-layout .right-column {
    height: 50%;
  }

  /* About Page Responsive Layout */
  .about-columns {
    display: flex;
  }

  .about-columns .left-column,
  .about-columns .right-column {
    display: none;
  }

  .about-columns .center-column {
    width: 100%;
  }
}